import React, { useCallback } from 'react'; // useState, useRef, useEffect 
import { motion } from 'framer-motion';
import { PracticeMode, PracticeHookResult, PracticeHighlightResult } from '../hooks/usePracticeCommons';
import { kigoMapping2, kigoMapping3 } from '../data/keymapData';
import { getKeyStyle, isLargeSymbol } from '../utils/styleUtils';

interface KeyboardLayoutProps {
  layerData: string[] | undefined;
  layoutIndex: number;
  layoutTitle: string;
  cols: number;
  fixedWidth: string;
  showKeyLabels: boolean;
  lastInvalidLayerIndex: number | null; // 
  lastInvalidKeyCode: number | null;
  activePractice: PracticeHookResult | null;
  isInvalidInputTarget: (targetPressCode: number, currentLayoutIndex: number, currentKeyIndex: number) => boolean; // App.tsx 
  practice: PracticeMode;
  currentFunctionKeyMap: Record<number, string>;
  training: boolean;
  className?: string;
  // clearInvalidHighlight?: () => void; // App.tsx
}

const KeyboardLayout: React.FC<KeyboardLayoutProps> = ({
  layerData,
  layoutIndex,
  layoutTitle,
  cols,
  fixedWidth,
  showKeyLabels,
  lastInvalidLayerIndex, // 
  lastInvalidKeyCode,
  activePractice,
  practice,
  currentFunctionKeyMap,
  training,
  className = '',
  isInvalidInputTarget, // props 
  // clearInvalidHighlight, // App.tsx
}) => {
    //  invalidKeyIndex  invalidTimerRef 

    // useEffect  (lastInvalidKeyCode  App.tsx )

    //  (renderKey)
    const renderKey = useCallback((key: string, idx: number) => {
        let originalKey = (key ?? '').trim();
        const isEmptyKey = originalKey === '';

        let k = originalKey; //  ()
        let highlightResult: PracticeHighlightResult = { className: null, overrideKey: null }; // highlightResult 
        // App.tsx  isInvalidInputTarget 
        let isInvalid = isInvalidInputTarget(idx, layoutIndex, idx); // isInvalid 
        //  App.tsx  lastInvalidKeyCode  lastInvalidLayerIndex 
        // targetPressCode (1)  idx 
        // if (isInvalidInputTarget(idx, layoutIndex, idx)) {
            // console.log(`[KeyboardLayout renderKey IS_INVALID_CHECK] isInvalidInputTarget (from props) returned TRUE for key ${originalKey} (idx ${idx}) on layer ${layoutIndex}`);
        //     isInvalid = true;
        // } else {
            // console.log(`[KeyboardLayout renderKey IS_INVALID_CHECK] isInvalidInputTarget (from props) returned FALSE for key "${originalKey}" (idx ${idx}) on layer ${layoutIndex}`);
        // }

        // ON (kigoMapping )
        if (training && showKeyLabels) {
            const funcKeyLabel = currentFunctionKeyMap[idx];

            // 
            if (practice === '' && layoutIndex === 3 && ['', ''].includes(originalKey)) {
                 k = originalKey; // 
            } else
            if (practice === '') {
                if (layoutIndex === 6) { // 1
                    if (isEmptyKey && funcKeyLabel) {
                        k = funcKeyLabel;
                    }
                } else if (layoutIndex === 2) { // 
                     k = '____';
                }
            } else if (practice === '') {
                if (layoutIndex === 7) {
                    if (funcKeyLabel) { // 
                        k = funcKeyLabel;
                    }
                    //  (kigoMapping2 )
                } else if (layoutIndex === 2) { // 
                    k = '____';
                }
            } else if (practice === '') {
                if (layoutIndex === 8) {
                    if (funcKeyLabel) { // 
                        k = funcKeyLabel;
                    }
                    //  (kigoMapping3 )
                } else if (layoutIndex === 2) { // 
                    k = '____';
                }
            } else if (layoutIndex === 3) { // 
                //  ( ____ )
                if (['1', '2', '3', '4'].includes(originalKey)) { // 
                    k = '____';
                }
            } else if (layoutIndex === 2) { //  ()
                 if (funcKeyLabel) {
                     k = funcKeyLabel;
                 }
            }
        }

        //  overrideKey 
        if (showKeyLabels && activePractice && typeof activePractice.getHighlightClassName === 'function') {
            console.log(`[KeyboardLayout] PRE-CALL getHighlightClassName for key: "${originalKey}", layoutIndex: ${layoutIndex}, keyIndex: ${idx}, activePractice.status: ${activePractice.status}, activePractice.targetChar: ${activePractice.targetChar}, currentTarget (if available):`, activePractice.currentTarget);
            try {
                highlightResult = activePractice.getHighlightClassName(originalKey, layoutIndex, idx);
            } catch (e) {
                console.error(`[KeyboardLayout] ERROR calling getHighlightClassName for key "${originalKey}" on layer ${layoutIndex}:`, e);
            }
        }

        // 
        let displayContent: string;
        if (!training) { // training  false OFF
            // OFF
            const defaultStyle = 'bg-white';
            let keyStyleClass = getKeyStyle(originalKey);

            if (originalKey === '' || originalKey === '\n') {
                keyStyleClass = defaultStyle;
            } else if (!keyStyleClass.includes('bg-')) {
                keyStyleClass = `${keyStyleClass} ${defaultStyle}`.trim();
            }

            return (
                <motion.div key={idx} className={`border rounded p-3 text-center text-sm shadow flex justify-center items-center ${keyStyleClass}`}
                    whileHover={{ scale: 1.05 }}
                    style={{ minHeight: '3rem', fontSize: isLargeSymbol(originalKey) ? '1.8rem' : undefined }}>
                    <code style={{ whiteSpace: "pre-line" }}>
                      {(originalKey ?? "").split(/\r?\n/).map((l: string, i: number, a: string[]) => (
                        <span key={i}>
                          {l}
                          {i < a.length - 1 && <br />}
                        </span>
                      ))}
                    </code>
                </motion.div>
            );
        }

        // ON
        if (showKeyLabels) {
            const finalKeyLabel = highlightResult.overrideKey ?? k;
            displayContent = finalKeyLabel === '' ? '\n' : finalKeyLabel; // overrideKey 
        } else {
            // OFF ____ 
            displayContent = isEmptyKey ? '\n' : '____';
        }

        // ON
        const finalClassName = `border rounded p-3 text-center text-sm shadow flex justify-center items-center whitespace-pre-line ${highlightResult.className ?? ''} ${isInvalid ? 'debug-invalid-key' : ''}`;
        if (originalKey === '' && layoutIndex === 2 && training) { // defaultTargetLayerIndex is 2
            console.log(`[KeyboardLayout "" FINAL_CLASS_CHECK] finalClassName: "${finalClassName}", highlightResult.className: "${highlightResult.className}"`);
        }
        return (
            <div
              key={idx}
              className={finalClassName}
              style={{ minHeight: '3rem', backgroundColor: isInvalid ? 'rgba(254, 202, 202, 0.5)' : undefined }} //  ()
            >
                {displayContent}
            </div>
        );
    }, [
        layoutIndex, showKeyLabels, activePractice, practice,
        currentFunctionKeyMap,
        training,
        lastInvalidKeyCode, // lastInvalidKeyCode 
        lastInvalidLayerIndex, // lastInvalidLayerIndex 
            isInvalidInputTarget, // 
    ]);

    if (!layerData) {
        return null; // 
    }

    // 
    return (
        <div className={`keyboard-layout bg-gray-100 p-2 rounded shadow-md ${className}`} style={{ width: fixedWidth }}>
            <h2 className="text-center font-semibold mb-2 text-sm">{layoutTitle}</h2>
            <div className={`grid gap-1`} style={{ gridTemplateColumns: `repeat(${cols}, minmax(0, 1fr))` }}>
                {layerData.map((key, index) => renderKey(key, index))}
            </div>
        </div>
    );
};

export default KeyboardLayout;
